<template>
  <div class="container-fluid my-3">
    <div class="row">
      <div class="col-6">
        <img class="img-fluid" :src="image.url" alt="" />
      </div>
      <div class="col-6">
        <div class="col-12">
          <h2 >
            {{ title }}
          </h2>
        </div>
        <div class="col-12"><prismic-rich-text :field="bodyText" /></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      title: null,
      image: [],
      bodyText: [],
    };
  },
  methods: {
    async getData(uid) {
      const data = await this.$prismic.client.getByUID("article", uid);

      this.title = data.data.title[0].text;
      this.image = data.data.image;
      this.bodyText = data.data.body_text;
    },
  },
  created() {
    this.getData(this.$route.params.uid);
  },
};
</script>