<template>

  <div
    class="
      col-12
      p-0
      m-0
      d-flex
      justify-content-center justify-content-md-start
      categories-banner
    "
  >
    <!-- All  -->
    <input type="checkbox" class="check" id="all" @click="setFilter" />
    <label
      for="all"
      class="btn btn-outline-light"
      :class="{ 'active-filter': filters.all }"
      >All</label
    >
    <!-- Event  -->
    <input type="checkbox" class="check" id="event" @click="setFilter" />
    <label
      for="event"
      class="btn btn-outline-light"
      :class="{ 'active-filter': filters.event }"
    >
      Event</label
    >
    <!-- Release  -->
    <input type="checkbox" class="check" id="release" @click="setFilter" />
    <label
      for="release"
      class="btn btn-outline-light"
      :class="{ 'active-filter': filters.release }"
    >
      Release</label
    >
    <!-- News  -->
    <input type="checkbox" class="check" id="news" @click="setFilter" />
    <label
      for="news"
      class="btn btn-outline-light"
      :class="{ 'active-filter': filters.news }"
    >
      News</label
    >


     <div class="marquee">
   
        <h5 class="m-0 banner-text">Supplying and
        supporting the best upcoming ear based entertainment in the South East.</h5>
       
  
    </div>

  </div>
  <router-link
    tag="div"
    :to="'/' + article.uid"
    v-for="(article, index) in filteredList"
    :key="index"
    class="col-12 col-md-6 article-card m-0 p-0"
  >
    <div class="article-card-text">
      <h5 class="mt-3 mb-0 mx-2">{{ article.data.title[0].text }}</h5>
      <p class="mx-2">
        <small>{{ article.data.category }}</small>
      </p>
    </div>

    <div
      :style="{ backgroundImage: 'url(' + article.data.image.url + ')' }"
      class="
        img-banner
        m-0
        p-0
        d-flex
        flex-column
        justify-content-center
        align-items-center
        ratio ratio-16x9
      "
    ></div>
  </router-link>
</template>
<script>
export default {
  data() {
    return {
      appearOptions: {
        threshold: 0,
        rootMargin: "0px 0px 100px 0px",
      },
      articles: [],
      filters: {
        all: true,
        event: false,
        release: false,
        news: false,
      },
    };
  },
  methods: {
    getData() {
      this.$prismic.client
        .get({
          predicates: this.$prismic.predicate.at("document.type", "article"),
        })
        .then((response) => {
          this.articles = response.results;
          this.$nextTick(() => {
            this.scrollIntoView();
          });
        });
    },
    setFilter(event) {
      //   window.scrollTo(0, 0);
      const inputId = event.target.id;
      const isActive = event.target.checked;
      const updatedFilters = {
        ...this.filters,
        [inputId]: isActive,
      };

      // this.filters = updatedFilters
      // console.log(this.filters)

      /**
       * Select only one option
       */

      // Convert to array
      //   let updatedFiltersArray = [];
      //   for (let item in updatedFilters) {
      //     updatedFiltersArray.push({
      //       item,
      //       value: updatedFilters[item],
      //     });
      //   }

      //   updatedFiltersArray.forEach((item) => {
      //     if (inputId === item.item) {
      //       item.value === !item.value;
      //     }
      //     if (item.item === "all") {
      //       item.value = false;
      //     }
      //     if (updatedFiltersArray.every((v) => v.value === false)) {
      //       if (item.item === "all") {
      //         item.value = true;
      //       }
      //     }
      //   });

      let updatedFiltersArray = [];

      for (let item in updatedFilters) {
        updatedFiltersArray.push({
          item,
          value: updatedFilters[item],
        });
      }
      // uncheck all
      let checkboxes = document.getElementsByClassName("check");

      for (let i = 0; i < checkboxes.length; i++) {
        if (checkboxes[i].checked == true) {
          checkboxes[i].checked = false;
        }
      }

      updatedFiltersArray.forEach((item) => {
        if (inputId === item.item) {
          item.value === !item.value;
        } else item.value = false;
      });

      // Convert back into object
      let object = {};
      for (let i = 0; i < updatedFiltersArray.length; i++) {
        object[updatedFiltersArray[i].item] = updatedFiltersArray[i].value;
      }
      this.filters = object;
    },
    scrollIntoView() {
      const faders = document.querySelectorAll(".fade-in");

      const appearOnScroll = new IntersectionObserver(function (
        entries,
        appearOnScroll
      ) {
        entries.forEach((entry) => {
          if (!entry.isIntersecting) {
            return;
          } else {
            entry.target.classList.add("appear");
            appearOnScroll.unobserve(entry.target);
          }
        });
      },
      this.appearOptions);

      faders.forEach((fader) => {
        appearOnScroll.observe(fader);
      });
    },
  },
  computed: {
    filteredList() {
      const posts = this.articles;

      return posts.filter((post) => {
        if (this.filters.event && post.data.category === "event") {
          return true;
        }
        if (this.filters.release && post.data.category === "release") {
          return true;
        }
        if (this.filters.news && post.data.category === "news") {
          return true;
        }
        if (this.filters.all) {
          return true;
        }
        return false;
      });
    },
  },
  created() {
    this.getData();
  },
};
</script>

