<template>
  <div class="container-fluid p-0 m-0">
    <SynthScene />
    <div class="row article-card-container m-0 p-0">
 <ArticleList />
    </div>
    <div class="row">
      <Calendar />
    </div>
  </div>
</template>
<script>
import SynthScene from "@/components/synth/SynthScene.vue";
import ArticleList from "@/components/home/ArticleList.vue";
import Calendar from "@/components/home/Calendar.vue";

export default {
  components: {
    SynthScene,
    ArticleList,
    Calendar
  },
  data() {
    return {
      appearOptions: {
        threshold: 0,
        rootMargin: "0px 0px 100px 0px",
      },
    };
  },
  methods: {
    getData() {
      this.$prismic.client
        .get({
          predicates: this.$prismic.predicate.at("document.type", "article"),
        })
        .then((response) => {
          this.articles = response.results;
          this.$nextTick(() => {
            this.scrollIntoView();
          });
        });
    },

    scrollIntoView() {
      const faders = document.querySelectorAll(".fade-in");

      const appearOnScroll = new IntersectionObserver(function (
        entries,
        appearOnScroll
      ) {
        entries.forEach((entry) => {
          if (!entry.isIntersecting) {
            return;
          } else {
            entry.target.classList.add("appear");
            appearOnScroll.unobserve(entry.target);
          }
        });
      },
      this.appearOptions);

      faders.forEach((fader) => {
        appearOnScroll.observe(fader);
      });
    },
  },
  
  created() {
    this.getData();
  },
};
</script>

<style scoped lang="scss">
.container-fluid {
  overflow: hidden;
}

</style>





