<template>
  <canvas class="webgl"></canvas>
</template>

<script>
import * as THREE from "three";

export default {
  data() {
    return {};
  },
  mounted() {
    /**
     * Shaders
     */

    const logoVertexShader =
      document.getElementById("logoVertexShader").textContent;
    const logoFragmentShader =
      document.getElementById("logoFragmentShader").textContent;
    const ccVertexShader =
      document.getElementById("ccVertexShader").textContent;
    const ccFragmentShader =
      document.getElementById("ccFragmentShader").textContent;
    const textVertexShader =
      document.getElementById("textVertexShader").textContent;
    const textFragmentShader =
      document.getElementById("textFragmentShader").textContent;

    let synthOn = false;

    /**
     * Base
     */

    // Canvas
    const canvas = document.querySelector("canvas.webgl");

    // Scene
    const scene = new THREE.Scene();

    /**
     * Textures
     */
    const textureLoader = new THREE.TextureLoader();
    const ccTexture = textureLoader.load(
      require("@/assets/textures/cc-texture.png")
    );

     const eventsTexture = textureLoader.load(
      require("@/assets/textures/events.png")
    );

    /**
     * Particles
     */
    const particlesGeometry = new THREE.BufferGeometry();
    const count = 200;
    const positions = new Float32Array(count * 3);
    for (let i = 0; i < count * 3; i++) {
      positions[i] = (Math.random() - 0.5) * 6;
    }
    particlesGeometry.setAttribute(
      "position",
      new THREE.BufferAttribute(positions, 3)
    );

    const particlesMaterial = new THREE.PointsMaterial({
      size: 0.02,
      sizeAttenuation: true,
    });
    // Points
    const particles = new THREE.Points(particlesGeometry, particlesMaterial);
    scene.add(particles);

    /**
     * Mesh
     */
    // Geometry
    const squigglesGeometry = new THREE.PlaneBufferGeometry(1.65, 1.65, 32, 32);

    const logoGeometry = new THREE.PlaneBufferGeometry(1.8, 1.8, 32, 32);

    const eventsGeometry = new THREE.PlaneBufferGeometry(1.0, 0.333, 32, 32);

    //  Squiggle material
    const squiggleMaterial = new THREE.ShaderMaterial({
      vertexShader: logoVertexShader,
      fragmentShader: logoFragmentShader,

      transparent: true,
      uniforms: {
        uTime: { value: 0 },
        uFrequency: { value: new THREE.Vector2(55, 60) },
        uSynth: {
          value: 0,
        },
        uSynthFreq: {
          value: 0,
        },
      },
    });
    // Logo material
    const logoMaterial = new THREE.ShaderMaterial({
      vertexShader: ccVertexShader,
      fragmentShader: ccFragmentShader,
      transparent: true,
      uniforms: {
        uTime: { value: 0 },
        uFrequency: { value: new THREE.Vector2(55, 60) },
        uTexture: {
          value: ccTexture,
        },
        uSynth: {
          value: 0,
        },
        uSynthFreq: {
          value: 0,
        },
      },

      polygonOffset: true,
      polygonOffsetFactor: -1.0,
      polygonOffsetUnits: -4.0,
    });

        // Events material
    const eventsMaterial = new THREE.ShaderMaterial({
      vertexShader: textVertexShader,
      fragmentShader: textFragmentShader,
      transparent: true,
      uniforms: {
        uTime: { value: 0 },
        uFrequency: { value: new THREE.Vector2(55, 60) },
        uTexture: {
          value: eventsTexture,
        },
        uSynth: {
          value: 0,
        },
        uSynthFreq: {
          value: 0,
        },
      },

      polygonOffset: true,
      polygonOffsetFactor: -1.0,
      polygonOffsetUnits: -4.0,
    });

    // Squiggle mesh
    const squiggleMesh = new THREE.Mesh(squigglesGeometry, squiggleMaterial);
    scene.add(squiggleMesh);
    squiggleMesh.rotation.z = Math.PI * 0.05;
    squiggleMesh.scale.set(0.47, 0.47, 0.47);
    squiggleMesh.position.set(-0.4, 0.0, 0.0);

    // Logo mesh
    const logoMesh = new THREE.Mesh(logoGeometry, logoMaterial);
    logoMesh.scale.set(0.495, 0.495, 0.495);
    logoMesh.position.set(-0.4, 0.0, 0.0);
    scene.add(logoMesh);


  // Text mesh
    const eventsMesh = new THREE.Mesh(eventsGeometry, eventsMaterial);
    // eventsMesh.scale.set(0.475, 0.475, 0.475);
    eventsMesh.position.set(0.37, 0.0, 0.0);
    scene.add(eventsMesh);

 

    /**
     * Sizes
     */
    const sizes = {
      width: window.innerWidth,
      height: window.innerHeight - (window.innerHeight / 100) * 40,
    };

    window.addEventListener("resize", () => {
      // Update sizes
      sizes.width = window.innerWidth;
      sizes.height = window.innerHeight - (window.innerHeight / 100) * 30;

      // Update camera
      camera.aspect = sizes.width / sizes.height;
      camera.updateProjectionMatrix();

      // Update renderer
      renderer.setSize(sizes.width, sizes.height);
      // renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    });

    /**
     * Camera
     */
    // Base camera
    const camera = new THREE.PerspectiveCamera(
      75,
      sizes.width / sizes.height,
      0.1,
      100
    );
    camera.position.set(0, 0, 0.7);
    scene.add(camera);

    // Controls

    /**
     * Renderer
     */
    const renderer = new THREE.WebGLRenderer({
      canvas: canvas,
    });
    renderer.setSize(sizes.width, sizes.height);
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

    // Clock
    const clock = new THREE.Clock();

    // Cursor movement

    let freq;
    const cursor = { x: 0, y: 0 };

    const changeFreq = () => {
      freq = ((cursor.x + 0.5) * 400) / 2 + 27.5;
      osc.frequency.value = freq;
      mainGainNode.gain.value = -(cursor.y - 0.5);
    };
    const mouseMoveChangeFreq = (x, y) => {
      cursor.x = x / sizes.width - 0.5;
      cursor.y = y / sizes.height - 0.5;
      if (synthOn) {
        changeFreq();
      }
    };
    canvas.addEventListener("mousemove", (e) => {
      let x = e.clientX;
      let y = e.clientY;
      mouseMoveChangeFreq(x, y);
    });
    canvas.addEventListener("touchmove", (e) => {
      e.preventDefault();
      let x = e.touches[0].clientX || e.changedTouches[0].pageX;
      let y = e.touches[0].clientY || e.changedTouches[0].pageY;
      mouseMoveChangeFreq(x, y);
    });

    /**
     * Synthesize
     */

    let audioContext = null;
    let mainGainNode = null;
    // let sineTerms = null;
    let osc = null;

    // // Setup
    // const setup = () => {
    //   mainGainNode = audioContext.createGain();
    //   mainGainNode.connect(audioContext.destination);
    //   mainGainNode.gain.value = 0.2;
    //   sineTerms = new Float32Array([0, 0, 1, 0, 1]);
    //   cosineTerms = new Float32Array(sineTerms.length);
    // }
    // setup();

    // Play tone
    const playTone = () => {
      osc = audioContext.createOscillator();
      osc.connect(mainGainNode);
      let type = "square";
      osc.type = type;
      osc.start();
      return osc;
    };

    // Stop tone
    const stopTone = () => {
      osc.stop();
      osc = null;
    };
    const startSynth = () => {
      synthOn = true;
      if (synthOn) {
        audioContext = new (window.AudioContext || window.webkitAudioContext)();
        mainGainNode = audioContext.createGain();
        mainGainNode.connect(audioContext.destination);
        mainGainNode.gain.value = 0.2;
        // sineTerms = new Float32Array([0, 0, 1, 0, 1]);
        // cosineTerms = new Float32Array(sineTerms.length);
        playTone();
        changeFreq();
        logoMaterial.uniforms.uSynth.value = 1;
        squiggleMaterial.uniforms.uSynth.value = 1;
                eventsMaterial.uniforms.uSynth.value = 1;
        
      }
    };

    const endSynth = () => {
      synthOn = false;
      if (!synthOn) {
        stopTone();
        logoMaterial.uniforms.uSynth.value = 0;
        squiggleMaterial.uniforms.uSynth.value = 0;
        eventsMaterial.uniforms.uSynth.value = 0;
      }
    };

    // Desktop
    canvas.addEventListener("mousedown", () => {
      startSynth();
    });
    canvas.addEventListener("mouseup", () => {
      endSynth();
    });

    // Mobile
    canvas.addEventListener("touchstart", () => {
      startSynth();
    });
    canvas.addEventListener("touchend", () => {
      endSynth();
    });

    /**
     * Animate
     */
    const tick = () => {
      // Elapsed time
      const elapsedTime = clock.getElapsedTime();
      // const deltaTime = elapsedTime - previousTime;
      // previousTime = elapsedTime;
      const timeLastTime = elapsedTime;

      // Particles
      particles.rotation.y = elapsedTime / 15;

      // Synthisize
      const randomValue = Math.random() * 2;

      //Squiggle synth
      squiggleMaterial.uniforms.uTime.value =
        Math.abs(Math.sin(elapsedTime) - timeLastTime) / 2 - elapsedTime * 3;
      squiggleMaterial.uniforms.uSynthFreq.value = randomValue;

      // Logo synth
      logoMaterial.uniforms.uTime.value =
        Math.abs(Math.sin(elapsedTime) - timeLastTime) / 2 - elapsedTime * 3;
      logoMaterial.uniforms.uSynthFreq.value = randomValue * 1.5;

      // Text synth
  
       eventsMaterial.uniforms.uTime.value =
        Math.abs(Math.sin(elapsedTime) - timeLastTime) / 2 - elapsedTime * 3;
      eventsMaterial.uniforms.uSynthFreq.value = randomValue * 1.5;
      

      // Cusor move
      // camera.position.x += (-cursor.x - camera.position.x * 2) * 0.05;
      // camera.position.y += (cursor.y - camera.position.y * 2) * 0.05;

      // camera.lookAt(scene.position);

      // Render
      renderer.render(scene, camera);

      // Call tick again on the next frame
      window.requestAnimationFrame(tick);
    };

    tick();
  },
};
</script>
<style>
canvas {
  position: relative;
  top: 0;
  left: 0;
  outline: none;
  user-select: none;
  display: block;
  overflow: hidden;
  touch-action: none;
}
canvas:hover {
  cursor: pointer;
}
</style>